export default {
  head() {
    return {
      title: `Homes around ${this.label}`
    }
  },
  mounted() {
    this.updateMap()
  },

  methods: {
    highlightMarker(homeId, isHighlighted) {
      document
        .getElementsByClassName(`home-${homeId}`)[0]
        ?.classList?.toggle('marker-highlight', isHighlighted)
    },
    updateMap() {
      this.$maps.showMap(
        this.$refs.map,
        this.lat,
        this.lng,
        this.getHomeMarkers()
      )
    },
    getHomeMarkers() {
      return this.homes.map((home) => {
        return {
          ...home._geoloc,
          pricePerNight: home.pricePerNight,
          id: home.objectID
        }
      })
    }
  },
  async beforeRouteUpdate(to, from, next) {
    const data = await this.$dataApi.getHomeByLocation(
      to.query.lat,
      to.query.lng
    )
    this.homes = data.json.hits
    this.label = to.query.lat
    this.lat = to.query.lng
    this.updateMap()
    next()
  },

  async asyncData({ query, $dataApi }) {
    const data = await $dataApi.getHomeByLocation(query.lat, query.lng)
    return {
      homes: data.json.hits,
      label: query.label,
      lat: query.lat,
      lng: query.lng
    }
  }
}
